import { makeStyles } from "@material-ui/core/styles";

export default makeStyles((theme) => ({
    mainContainer: {
        width: "100%",
        margin: "0",
        paddingTop:"20px",
        paddingBottom:"60px",
        // backgroundColor:"#212529",
        // color:"white"
    
      },
      specialization:{
        textAlign:"left",
        width:"100%",
        margin:"0",
        fontSize:"2.371rem",
        fontWeight:"600",
        fontFamily:"Montserrat"
      },
      my:{
        paddingLeft:"125px",
        color: "#f9004D",

      },
      github:{
          height:"50px",
          width:"110px",
          border: '4px solid #f9004D',
          backgroundColor: '#212529',
          color: 'white',

      },
      heading:{
        color: "#f9004D",
      },
     
}));



